'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property' ;


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name Number
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/ctor}
*/
import Number from './../ctor';
setReadOnly( ns, 'Number', Number );

/**
* @name float32
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32}
*/
import float32 from './../float32';
setReadOnly( ns, 'float32', float32 );

/**
* @name float64
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float64}
*/
import float64 from './../float64';
setReadOnly( ns, 'float64', float64 );

/**
* @name int32
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/int32}
*/
import int32 from './../int32';
setReadOnly( ns, 'int32', int32 );

/**
* @name uint8
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/uint8}
*/
import uint8 from './../uint8';
setReadOnly( ns, 'uint8', uint8 );

/**
* @name uint16
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/uint16}
*/
import uint16 from './../uint16';
setReadOnly( ns, 'uint16', uint16 );

/**
* @name uint32
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/uint32}
*/
import uint32 from './../uint32';
setReadOnly( ns, 'uint32', uint32 );


// EXPORTS //

export default ns;
